@keyframes motionAV {
  50% {
    background-position: 30%;
    transform: rotate(1deg);
  }

  100% {
    background-position: center;
    transform: rotate(0deg);
  }
}

@keyframes motionAVM {
  50% {
    background-position: 5%;
  }

  100% {
    background-position: center;
  }
}

@keyframes motionAVX1 {
  50% {
    transform: rotate(12deg);
  }

  100% {
    transform: rotate(8deg);
  }
}

@keyframes motionAVX2 {
  50% {
    transform: rotate(24deg);
  }

  100% {
    transform: rotate(18deg);
  }
}

.about-section {
  min-height: unset;
  height: min-content;
  padding: 4rem 0rem;
}

.about-container {
  position: relative;
  //max-width: 1000px;
  width: 100vw;
  height: min-content;
  padding: 5rem 0rem;
  z-index: 2;
  max-width: 1100px;
  gap: 5vw;

  display: flex;
  flex-direction: row;
  flex-basis: 100%;
  //background: red;

  & > * {
    width: 100%;
    display: flex;
    align-items: center;
  }
  & > :nth-child(1) {
    justify-content: center;
    // background: red;

    .img-wrapper {
      position: relative;
      //background: blue;
      width: 100%;
      max-width: 200px;
      margin-left: 150px;
      aspect-ratio: 3 / 4;

      @supports not (aspect-ratio: 3 / 4) {
        height: 100% * 3.5 / 4;
      }

      div {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        max-width: 200px;
        aspect-ratio: 3 / 4;

        @supports not (aspect-ratio: 3 / 4) {
          height: 100% * 3.5 / 4;
        }
        background-image: url("../assets/avatar.jpg");
        background-position: center;
        background-size: cover;
        border-radius: 30px 10px 40px 10px;
        
        animation: motionAV 6s cubic-bezier(0.46, 0.03, 0.52, 0.96) infinite;

        &::after {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: var(--clr-accM);
          opacity: .7;
          filter: contrast(1.5);
          border-radius: inherit;
        }
      }

      $color: hsl(222, 93%, 71%);

      &::before, &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: $color;
        opacity: .15;
        filter: contrast(1.5);
        transform: scale(1) rotate(8deg);
        border-radius: inherit;
        z-index: -11111;
        isolation: isolate;
        border-radius: 30px 10px 40px 10px;
        animation: motionAVX1 7s cubic-bezier(0.46, 0.03, 0.52, 0.96) infinite;
      }

      &::after {
        transform: scale(1) rotate(18deg);
        animation: motionAVX2 10s cubic-bezier(0.46, 0.03, 0.52, 0.96) infinite;
      }
    }
  }
  & > :nth-child(2) {
    // background-color: lightcoral;
    div {
    }

    h1 {
      margin-bottom: 0.6rem;
      font-size: 3em;
    }

    p {
      max-width: 35ch;
      color: var(--clr-txtS);
      line-height: 2rem;
      word-spacing: 2px;
      font-weight: 300;

      span {
        font-weight: 700;
        color: var(--clr-txtM);
      }
    }
  }
}

.about-container-mobile {
  position: relative;
  width: 100vw;
  height: min-content;
  padding: 5rem 0rem;
  z-index: 2;
  max-width: 1100px;
  gap: 5vw;

  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  //background: red;

  & > * {
    height: min-content;
    width: inherit;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .sidebar-container {
    all: unset;
    //background: red;
    position: relative;
    display: grid;
    place-items: center;
    width: 100vw;
    margin-bottom: 3rem;

    .img-wrapper {
      position: relative;
      width: 100%;
      max-width: 280px;
      aspect-ratio: 4 / 3;
      @supports not (aspect-ratio: 4 / 3) {
        height: 13rem;
      }

      div {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        max-width: 280px;
        aspect-ratio: 4 / 3;
        @supports not (aspect-ratio: 4 / 3) {
          height: 13rem;
        }

        background-image: url("../assets/avatar.jpg");
        background-position: center;
        background-size: 120%;
        border-radius: 30px 10px 40px 10px;

        animation: motionAVM 6s cubic-bezier(0.46, 0.03, 0.52, 0.96) infinite;

        &::after {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: var(--clr-accM);
          opacity: .7;
          filter: contrast(1.5);
          border-radius: inherit;
        }
      }

      $color: hsl(222, 93%, 71%);

      &::before, &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: $color;
        opacity: .15;
        filter: contrast(1.5);
        transform: scale(1) rotate(8deg);
        border-radius: inherit;
        z-index: -11111;
        isolation: isolate;
        border-radius: 30px 10px 40px 10px;
        animation: motionAVX1 7s cubic-bezier(0.46, 0.03, 0.52, 0.96) infinite;
      }

      &::after {
        transform: scale(1) rotate(18deg);
        animation: motionAVX2 10s cubic-bezier(0.46, 0.03, 0.52, 0.96) infinite;
      }



      
    }
  }
  & > :nth-child(2) {
    //background-color: lightcoral;
    padding: 1rem 3rem;
    h1 {
      margin-bottom: 0.6rem;
      font-size: 2.5em;
    }

    p {
      max-width: 33ch;
      color: var(--clr-txtS);
      line-height: 2rem;
      word-spacing: 2px;
      font-weight: 300;

      span {
        font-weight: 600;
        color: var(--clr-txtM);
      }
    }
  }
}
